@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

*::before, *::after, *{
  box-sizing: border-box;
  list-style: none;
}
body, * {
  margin: 0;
  font-family: 'Poppins',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-decoration: none;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* navbar css */
.navbar{
  top: 0;
  width: 100%;
  background-color: transparent;
  z-index: 10;
  position: fixed;
  transition: 0.3s ease;
}
.navbar.active{
  background-color: #171123f6;
  box-shadow: 0px 10px 42px -11px rgba(0,0,0,0.75);
-webkit-box-shadow: 2px 10px 42px -11px rgba(0,0,0,0.75);
-moz-box-shadow: 2px 10px 42px -11px rgba(0,0,0,0.75);
}
.navbar .links{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  margin-right: 2rem;
  flex-wrap: nowrap;
}

.links > a{
  color: #fff;
  cursor: pointer;
  padding: 1.5rem;
  font-size: 1.3em;
}
.links > a.active{
  padding: 0.8rem 1.5rem;
}

.links > a:hover{
  background-color: #616A6B;
}

/* home css */

.home{
  background-color: #171123;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 105vh;
  row-gap: 30px;
  border-bottom: 5px solid #3498DB;
}

.home h1{
  color: whitesmoke;
  font-family: Helvetica, sans-serif, Arial;
  font-size: 3rem;
  font-weight: 1000;
}

.home h2{
  color: #3498DB;
  font-family: calibri;
  font-style: italic;
  font-weight: 10;
  font-size: 2.3rem;
}

.home button{
  padding: 15px;
  border: 1px solid #fff;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  font-size: 1.6rem;
  cursor: pointer;
  transition: 0.75s ease-in-out;
}

.home button:hover{
  background-color: #fff;
  color: #171123;

}

.particles{
  background-color: red;
  position: absolute;
}



/* profile style */

.profile{
  display: flex;
  flex-direction: column;
  margin: 10rem 12rem;
  height: auto;
  row-gap: 3rem;
  z-index: 9;
  font-family: 'Poppins';
  color: #171123;
}
 
.profile > h1{
  text-align: center;
  font-size: 2.3em;
  border-bottom: 1px solid;
  padding-bottom: 1rem;
  font-weight: bolder;
  letter-spacing: 1px;
} 

 .profile-content{
  display: flex;
  flex-direction: row;
  column-gap: 20px;
  height: 70%;
  width: auto;
  position: relative;
}

.profile-content > img{
  width: auto;
  height: 15rem;
  position: relative;
  border: 5px solid;
  border-radius: 50%;
  padding: 10px;
}

.profile-description{
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-radius: 20px;
  box-shadow: 0 16px 24px 0px rgb(0 0 0 / 0%), 0 6px 30px 0px rgb(0 0 0 / 0%), 0 8px 10px 0px rgb(0 0 0 / 10%);
} 

.profile-description > h2{
  font-weight: 900;
  color: #171123;
}

span{
  color: #3498DB;
}

.profile-description > p{
  margin-top: 2em;
  font-weight: 300;
  font-size: 1.4em;
}

.icons{
  display: flex;
  flex-direction: row;
  column-gap: 9px;
  margin-top: 1em;
}

.icons > a {
  margin: 10px;
  color: #171123;
  font-size: 1.8em; 
  transition: 0.5s ease-in-out;
}

.icons > a:hover{
    /* transform: translateY(15px); */
    color: #3498DB;
    transform: scale(2);
}

.profile-info{
  color: #171123;
  display: grid;
  gap: 2rem;
  grid-template-areas: "educ certs"
                      "skills skills";
}

.profile-info > a{
  color: #171123;
}

.profile-info > div > h2{
  margin-bottom: 1rem;
}



/* Offer styles */
.offer-section{
  display: flex;
  position: relative;
  flex-direction: column;
  z-index: 9;
  text-align: center;
  margin: 0 10%;
  margin-bottom: 10%;
}

.offer-section > h1{
  text-align: center;
  border-bottom: 1px solid;
  padding-bottom: 1rem;
  font-weight: bolder;
  letter-spacing: 1px;
  margin-bottom: 6rem;
}

.offers{
  display: flex;
  flex-direction: row;
  text-align: justify;
  justify-content: center;
  column-gap: 2rem;
}

.offer-1, .offer-2, .offer-3{
  display: flex;
  flex-direction: column;
  width: 30%;
}

.offer-1 > h1, .offer-2 > h1, .offer-3 > h1{
  font-size: 1rem;
}


/* works style */
.works{
  width: 100%;
  height: auto;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  padding: 55px 0;
  z-index: 9;
}

.works-heading > h1{
  border-bottom: 1px solid;
  padding: 0rem 3rem 1rem 3rem;
  padding-bottom: 1rem;
  color: #171123;
  letter-spacing: 1px;
}

.works-content{
  width: 80%;
  display: grid;
  grid-template-columns: repeat(4,auto);
  grid-template-rows: repeat(3,2fr);
  margin-top: 3rem;
  grid-gap: 30px;
}

.works-content > .img-div{
  box-shadow: 0 16px 24px 2px rgb(0 0 0 / 14%), 0 6px 30px 5px rgb(0 0 0 / 12%), 0 8px 10px -7px rgb(0 0 0 / 20%);
  border-radius: 25px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  transition: linear 0.5s ease-in;
  height: 220px;
}

.img-div > img{
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: all .5s ease-in-out;
  z-index: 2;
}

.img-div > a{
  z-index: 3;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
  padding: 10px 30px;
  background: transparent;
  color: #212529;
  border: 2px solid #ff013c;
  font-size: 20px;
  font-weight: 500;
  text-transform: uppercase;
  width: -webkit-max-content;
  width: max-content;
  text-decoration: none;
  position: absolute;
  transition:all .5s ease-in-out;
  top: 50%;
  visibility: hidden;

}

.img-div:hover > img{
  opacity: 0.1;
}

.img-div:hover > a{
  visibility: visible;
}

.img-div > a:hover {
  background-color: #ff013c;
  color: #fff;
  box-shadow: 0 16px 24px 2px rgb(227 28 110 / 40%), 0 6px 30px 5px rgb(227 28 110 / 12%), 0 8px 10px -7px rgb(0 0 0 / 20%);

}

.works-buttons{
  display: flex;
  flex-direction: row;
  margin-top: 2em;
  column-gap: 2rem;
}

.works-buttons > button, .works-buttons > button::after{
  padding: 16px 50px;
 font-size: 18px;
 background: linear-gradient(45deg, transparent 5%, #ff013c 5%);
 border: 0;
 color: #fff;
 letter-spacing: 3px;
 line-height: 1;
 box-shadow: 6px 0px 0px #00e6f6;
 outline: transparent;
 position: relative;
 cursor: pointer;
}

.works-buttons > button::after{
  --slice-0: inset(50% 50% 50% 50%);
 --slice-1: inset(80% -6px 0 0);
 --slice-2: inset(50% -6px 30% 0);
 --slice-3: inset(10% -6px 85% 0);
 --slice-4: inset(40% -6px 43% 0);
 --slice-5: inset(80% -6px 5% 0);
 content: "HOVER ME";
 display: block;
 position: absolute;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;
background: linear-gradient(45deg, transparent 3%, #00e6f6 3%, #00e6f6 5%, #ff013c 5%);
text-shadow: -3px -3px 0px #f8f005, 3px 3px 0px #00e6f6;
clip-path: var(--slice-0);
}

.works-buttons > button:hover::after{
  animation: 1s glitch;
  animation-timing-function: steps(2, end);
}

@keyframes glitch {
  0% {
  clip-path: var(--slice-1);
  transform: translate(-20px, -10px);
  }
  
   10% {
   clip-path: var(--slice-3);
    transform: translate(10px, 10px);
   }
 
  20% {
    clip-path: var(--slice-1);
    transform: translate(-10px, 10px);
  }
  
   30% {
    clip-path: var(--slice-3);
    transform: translate(0px, 5px);
   }
  
   40% {
    clip-path: var(--slice-2);
   transform: translate(-5px, 0px);
   }
  
   50% {
    clip-path: var(--slice-3);
    transform: translate(5px, 0px);
   }
  
   60% {
    clip-path: var(--slice-4);
    transform: translate(5px, 10px);
   }
  
   70% {
   clip-path: var(--slice-2);
    transform: translate(-10px, 10px);
   }
  
   80% {
   clip-path: var(--slice-5);
    transform: translate(20px, -10px);
   }
  
   90% {
    clip-path: var(--slice-1);
    transform: translate(-10px, 0px);
   }
  
   100% {
    clip-path: var(--slice-1);
    transform: translate(0);
   }
  }

