/* Responsive */

@media (max-width: 1071px) {
  body{
    font-size: 10px;
  }
  .works-content{
    grid-template-columns: repeat(3,auto);
    grid-template-rows: repeat(auto,2fr);
  }
}

@media (max-width: 768px) {

  .works-content{
    grid-template-columns: repeat(2,auto);
    grid-template-rows: repeat(auto,2fr);
  }
  .profile-content{
    flex-direction: column;
  }
  .works{
    margin-top: 180px;
  }
  .profile-content > img{
    width: 50%;
    align-items: center;
    margin-left: 25%;
  }
  
  .profile{
    margin: 5rem;
  }
}

@media (max-width: 425px) {
  .navbar .links{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
    margin-right: 0;
  }

  /*  */
  
  .home h1{
    text-align: center;
  }
  
  .home h2{
    text-align: center;
  }
  
  .home button{
    font-size: 1.3rem;
  }
  
  /*  */

  .works-content{
    grid-template-columns: repeat(1,auto);
    grid-template-rows: repeat(auto,2fr);
  }
  .profile{
    margin: 4rem 2rem;
  }
  .profile-content > img{
    width: 80%;
    align-items: center;
    margin-left: 10%;
  }
  .offers{
    flex-direction: column;
    row-gap: 2rem;
  }
  .offer-1, .offer-2, .offer-3{
    width: 100%;
  }
  .profile-info{
    color: #171123;
    display: grid;
    gap: 2rem;
    font-size: 12px;
    grid-template-areas: 
                        "educ" 
                        "certs"
                        "skills";
  }
  
  .personal{
    column-gap: 0;
    font-size: 1.2em;
    padding-bottom: 2em;
  }
}


@media (max-width: 320px) {
  body{
    align-items: center;
    text-align: center;
    box-sizing: border-box;
  }
  .profile-content > img{
    width: 100%;
    align-items: center;
    margin-left: 0;
  }
}
